.main-header {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  position: relative;
  z-index: 60;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}
@media (min-width: 1200px) {
  .main-header::before {
    content: "";
    left: 0;
    top: 0;
    width: 4px;
    z-index: -60;
    position: absolute;
    height: 100%;
    box-shadow: -5px 0 10px 0 #a8a8a8;
  }
  .main-header::after {
    content: "";
    right: 0;
    top: 0;
    width: 4px;
    z-index: -60;
    position: absolute;
    height: 100%;
    box-shadow: 5px 0 10px 0 #a8a8a8;
  }
}
.main-header__logo {
  position: absolute;
  left: 50%;
  margin-left: -92.5px;
}
.main-header__footer {
  margin: 0 auto;
}
.main-nav {
  display: flex;
  width: 100%;
  padding: 0;
  margin: 0 auto;
  position: relative;
  min-height: 56px;
}
@media (min-width: 768px) {
  .main-nav {
    width: 768px;
    z-index: -1;
  }
}
@media (min-width: 1200px) {
  .main-nav {
    width: 1200px;
    background-color: #ffffff;
  }
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__toggle {
    display: block;
    z-index: 2;
    width: 23px;
    height: 22px;
    margin-left: auto;
    border: 0;
    background-color: #ffffff;
    position: relative;
    padding: 0;
    top: 17px;
    right: 20px;
    cursor: pointer;
  }
  .main-nav--closed .main-nav__toggle::after {
    content: "";
    height: 4px;
    width: 100%;
    background-color: #81b3d2;
    position: absolute;
    top: 0;
    left: 0;
    box-shadow: 0 9px 0 0 #81b3d2, 0 18px 0 0 #81b3d2;
  }
  .main-nav--closed .main-nav__toggle:hover::after {
    opacity: 0.6;
  }
  .main-nav--closed .main-nav__toggle:active::after {
    opacity: 0.3;
  }
  .main-nav--opened .main-nav__toggle {
    display: block;
    z-index: 2;
    padding: 0;
    width: 23px;
    height: 22px;
    margin-left: auto;
    border: 0;
    background-color: transparent;
    position: absolute;
    right: 20px;
    top: 17px;
  }
  .main-nav--opened .main-nav__toggle::after,
  .main-nav--opened .main-nav__toggle::before {
    content: "";
    height: 4.2px;
    width: 27px;
    background-color: #ffffff;
    position: absolute;
    top: 8.5px;
    left: -2px;
    box-shadow: none;
  }
  .main-nav--opened .main-nav__toggle::after {
    transform: rotate(45deg);
  }
  .main-nav--opened .main-nav__toggle::before {
    transform: rotate(-45deg);
  }
  .main-nav--opened .main-nav__toggle:hover {
    opacity: 0.6;
  }
  .main-nav--opened .main-nav__toggle:active {
    opacity: 0.3;
  }
  .main-nav--closed .main-nav__list {
    display: none;
  }
  .main-nav--opened .main-nav__list {
    display: flex;
    z-index: 2;
    position: absolute;
  }
}
@media (max-width: 767px) {
  .main-nav--nojs .main-nav__list {
    display: flex;
    z-index: 2;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
